
import slide from 'components/slide/index'
import http from 'js/http'
import uploadImg from 'components/uploadImg/index'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import {mapState, mapMutations, mapGetters} from 'vuex'

export default {
name: "index",
directives: {
TransferDom
},
components: {
slide,
uploadImg,
Confirm,
InlineLoading
},
data() {
return {
logoSrc: '',
form: {
logo: '',
name: '',
desc: '',
scheme: ''
},
show:false,
saveLoad:false
}
},
computed: {
...mapGetters([
'myNodeInfo'
]),
},
methods: {
noSave(){
this.$router.back()
},
handleSuccessLogo(res, src) {
this.logoSrc = src
this.form.logo = res
},
backPage() {
for (let i in this.form) {

if (this.form[i] !== this.myNodeInfo[i]) {
this.show = true
return
}
}
this.$router.back()
},
handleSave(){
this.saveLoad = true
http.post('/node/edit',this.form,(res)=>{
this.saveLoad=false
if (res.code!==0){
this.$vux.toast.show(res.msg)
return
}
let newData = Object.assign({},this.myNodeInfo,this.form)
localStorage.setItem('myNodeInfo',JSON.stringify(newData))
this.setMyNodeInfo(newData)
this.$vux.toast.show({
text: res.msg,
type: 'success'
})
setTimeout(() => {
this.$router.back()
}, 1500)
})

},
vaildFrom(){
if (!this.form.name){
this.$vux.toast.show('节点名称不能为空')
return
}
if (!this.form.desc){
this.$vux.toast.show('节点简介不能为空')
return
}
if (this.form.desc.length>1000){
this.$vux.toast.show('节点简介内容超出限制')
return
}
if (!this.form.scheme){
this.$vux.toast.show('建设方案不能为空')
return
}
if (this.form.scheme.length>1000){
this.$vux.toast.show('建设方案内容超出限制')
return
}
this.handleSave()
},
...mapMutations({
setMyNodeInfo: 'MY_NODE_INFO'
})
},
created() {
for (let i in this.form) {
this.form[i] = this.myNodeInfo[i] || ''
}
this.logoSrc = this.myNodeInfo.logo||'/static/images/node-avatar-default.jpg'
}
}



